import { initializeApp } from 'firebase/app';
import { getDatabase, ref, onValue, get, set } from 'firebase/database';

// Your web app's Firebase configuration
const firebaseConfig = {
  databaseURL: "https://lupus-in-tabula-157a0-default-rtdb.europe-west1.firebasedatabase.app",
  apiKey: "AIzaSyANRDc-IKoiTE5b2XSniQv2iz53kaU-YY4",
  authDomain: "lupus-in-tabula-157a0.firebaseapp.com",
  projectId: "lupus-in-tabula-157a0",
  storageBucket: "lupus-in-tabula-157a0.firebasestorage.app",
  messagingSenderId: "527839463153",
  appId: "1:527839463153:web:d8b54f6eed0feb2cbc4b50"
};

// Initialize Firebase
let app;
let database;

async function testDatabaseConnection() {
  try {
    console.log('Testing database connection...');
    const testRef = ref(database, 'test');
    
    // Try to write a test value
    await set(testRef, { timestamp: Date.now() });
    console.log('Database write test successful');
    
    // Try to read the test value
    const snapshot = await get(testRef);
    console.log('Database read test successful:', snapshot.val());
    
    return true;
  } catch (error) {
    console.error('Database connection test failed:', error);
    return false;
  }
}

try {
  console.log('Initializing Firebase...');
  app = initializeApp(firebaseConfig);
  database = getDatabase(app);
  console.log('Firebase app initialized');
  
  // Test the connection
  testDatabaseConnection().then(success => {
    if (success) {
      console.log('Firebase Realtime Database is working correctly');
    } else {
      console.error('Firebase Realtime Database connection failed');
    }
  });
  
  console.log('Firebase initialization complete');
} catch (error) {
  console.error('Error initializing Firebase:', error);
  throw error;
}

export { database }; 