import React from 'react';
import { QRCodeSVG } from 'qrcode.react';

export const QRCode = ({ gameCode }) => {
  const gameUrl = `https://rmarchet.github.io/lupus/?code=${gameCode}`;

  return (
    <div className="qr-code-container">
      <QRCodeSVG
        value={gameUrl}
        size={200}
        level="H"
        includeMargin={true}
        className="qr-code"
      />
      <p className="qr-code-text">Scan to join the game</p>
      <p className="qr-code-url">{gameUrl}</p>
    </div>
  );
}; 