import React, { useState } from 'react';

export const Setup = ({ onGameStart }) => {
  const [config, setConfig] = useState({
    totalPlayers: 8,
    werewolves: 2,
    seers: 1,
    bodyguards: 1,
    gameCode: Math.random().toString(36).substring(2, 8).toUpperCase(),
    gameMasterName: 'Game Master'
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('Setup: Submitting form with config:', config);
    try {
      await onGameStart({
        ...config,
        players: [config.gameMasterName] // Start with the game master as the first player
      });
      console.log('Setup: Game start completed');
    } catch (error) {
      console.error('Setup: Error starting game:', error);
      alert('Failed to start game: ' + error.message);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setConfig(prev => ({
      ...prev,
      [name]: parseInt(value, 10)
    }));
  };

  return (
    <div className="setup-form">
      <h2>Game Setup</h2>
      <p className="game-code">Game Code: <strong>{config.gameCode}</strong></p>
      <p className="setup-instructions">Share this code with players to join the game</p>
      
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="gameMasterName">Your Name (Game Master)</label>
          <input
            type="text"
            id="gameMasterName"
            name="gameMasterName"
            value={config.gameMasterName}
            onChange={(e) => setConfig(prev => ({ ...prev, gameMasterName: e.target.value }))}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="totalPlayers">Total Players (6-16)</label>
          <input
            type="number"
            id="totalPlayers"
            name="totalPlayers"
            min="6"
            max="16"
            value={config.totalPlayers}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="werewolves">Number of Werewolves</label>
          <input
            type="number"
            id="werewolves"
            name="werewolves"
            min="1"
            max="4"
            value={config.werewolves}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="seers">Number of Seers</label>
          <input
            type="number"
            id="seers"
            name="seers"
            min="0"
            max="1"
            value={config.seers}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="bodyguards">Number of Bodyguards</label>
          <input
            type="number"
            id="bodyguards"
            name="bodyguards"
            min="0"
            max="1"
            value={config.bodyguards}
            onChange={handleChange}
            required
          />
        </div>

        <div className="role-descriptions">
          <h3>Role Descriptions</h3>
          <ul>
            <li><strong>Werewolves:</strong> Eliminate one player each night. Work together to eliminate all villagers.</li>
            <li><strong>Seers:</strong> Check one player's role each night to determine if they are a werewolf.</li>
            <li><strong>Bodyguards:</strong> Protect one player each night from being eliminated. Cannot protect the same player twice in a row.</li>
            <li><strong>Villagers:</strong> Regular players trying to identify and eliminate the werewolves.</li>
          </ul>
        </div>

        <button type="submit" className="start-game-btn">Start Game</button>
      </form>
    </div>
  );
}; 